body {
  font-family: "Press Start 2P", cursive;
  background-color: black;
  background-image: url("/src/Images/fondo-screen.jpg");
  background-size: auto;
  background-position: center;
  background-repeat: no-repeat; }

.pokedex {
  position: relative;
  width: 320px;
  height: 600px;
  background-color: transparent;
  margin: 100px auto; }

.screen {
  width: 170px;
  height: 170px;
  background-image: url("/src/Images/fondopokedex.gif");
  position: absolute;
  top: 200px;
  left: 75px;
  z-index: 1;
  cursor: pointer; }

img {
  width: 100%;
  height: 100%;
  object-fit: cover; }

.num {
  position: absolute;
  top: 209px;
  left: 84px;
  z-index: 2;
  border: 3px double #005441; }

.num p {
  font-size: 20px;
  color: black;
  margin: 0;
  padding: 3px;
  background-color: white; }

.pokemon {
  position: absolute;
  top: 320px;
  left: 84px;
  z-index: 3;
  font-size: 0.8rem;
  border: 3px double #005441;
  padding: 6px;
  color: black;
  background-color: white; }

.fondo-pokedex {
  position: absolute;
  width: 100%;
  height: 184px;
  top: 193px;
  z-index: 0;
  background-color: transparent;
  backdrop-filter: blur(10px); }

.face-up {
  width: 100%;
  height: 200px;
  background-image: linear-gradient(rgba(255, 123, 0, 0.363), rgba(255, 106, 0, 0.363)), url("/src/Images/fondo-caratula.jpg");
  position: absolute;
  top: 87px;
  z-index: 4;
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
  border: 1px solid black; }

.face-down {
  width: 100%;
  height: 200px;
  background-image: linear-gradient(rgba(255, 123, 0, 0.363), rgba(255, 106, 0, 0.363)), url("/src/Images/fondo-caratula.jpg");
  position: absolute;
  top: 286px;
  z-index: 4;
  border-bottom-left-radius: 6px;
  border-bottom-right-radius: 6px;
  border: 1px solid black; }

.pokebola {
  position: relative;
  width: 150px;
  height: 150px;
  background-color: red;
  border-radius: 50%;
  overflow: hidden;
  transform: translate(81px, 206px);
  z-index: 5;
  top: 1px;
  border: 3px solid black; }

.red {
  width: 100%;
  height: 73px;
  background-image: url("/src/Images/fondo-caratula.jpg");
  border-bottom: 10px solid black; }

.white {
  position: absolute;
  width: 100%;
  height: 73px;
  background-color: white;
  bottom: -6px; }

.play {
  position: absolute;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: white;
  border: 10px solid black;
  top: 59px;
  left: 55px;
  transition: 0.5s;
  cursor: pointer; }

.play:hover {
  background-color: #ff7940;
  transition: 0.5s; }

audio {
  position: fixed;
  visibility: hidden; }
